// components/Modules/MarketNewsSection.jsx
"use client";

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import axios from 'axios'; 
// ‡§Ü‡§ï‡§∞‡•ç‡§∑‡§ï ‡§Ü‡§á‡§ï‡§®‡•ã‡§Ç ‡§ï‡•á ‡§≤‡§ø‡§è ‡§≤‡•ã‡§ï‡§™‡•ç‡§∞‡§ø‡§Ø ‡§≤‡§æ‡§á‡§¨‡•ç‡§∞‡•á‡§∞‡•Ä
import { Zap, Clock, BookOpen, ChevronLeft, ChevronRight, Filter, TrendingUp } from 'lucide-react'; 
import { slugify } from '../../../src123/data/Data';

const DEFAULT_LIMIT = 9;

// API ‡§∏‡•á ‡§°‡•á‡§ü‡§æ ‡§≤‡§æ‡§®‡•á ‡§ï‡•á ‡§≤‡§ø‡§è ‡§´‡§Ç‡§ï‡•ç‡§∂‡§® (‡§™‡§ø‡§õ‡§≤‡•á ‡§µ‡§∞‡•ç‡§ú‡§º‡§® ‡§ú‡•à‡§∏‡§æ ‡§π‡•Ä)
const fetchNews = async (page, limit) => {
    try {
        const response = await axios.get(`http://localhost:5000/api/news?page=${page}&limit=${limit}`); 
        const data = response.data;
        return data || { news: [], totalPages: 1, totalItems: 0 };
    } catch (error) {
        console.error("Error fetching news data:", error);
        return { news: [], totalPages: 1, totalItems: 0 };
    }
};

// --- ‡§π‡•á‡§≤‡•ç‡§™‡§∞ ‡§´‡§Ç‡§ï‡•ç‡§∂‡§®‡•ç‡§∏ ---
const extractCategories = (data) => {
    const categories = new Set();
    data.news.forEach(item => {
        if (item.category) {
            categories.add(item.category);
        }
    });
    return Array.from(categories).sort();
};

// ----------------------------------------------------------------------

const MarketNewsFilters = ({ activeCategory, setActiveCategory, categories }) => {
    const filterCategories = ["All", ...categories];

    return (
        <div className="flex flex-wrap gap-2 text-sm items-center">
             <Filter className="w-4 h-4 text-emerald-600 dark:text-emerald-400" />
            {filterCategories.map((cat) => (
                <button
                    key={cat}
                    onClick={() => setActiveCategory(cat)}
                    // ‡§Ü‡§ï‡§∞‡•ç‡§∑‡§ï 'Chip' ‡§°‡§ø‡§ú‡§º‡§æ‡§á‡§® - 3D/Elevated Shadow
                    className={`rounded-full px-3 py-1 font-medium transition duration-200 border ${activeCategory === cat
                            ? "bg-emerald-600 border-emerald-700 text-white shadow-lg shadow-emerald-500/50 hover:bg-emerald-700"
                            : "bg-white border-slate-300 text-slate-700 hover:bg-emerald-50/50 hover:shadow-md dark:bg-slate-800 dark:border-slate-700 dark:text-slate-200 dark:hover:bg-slate-700"
                        }`}
                >
                    {cat}
                </button>
            ))}
        </div>
    );
};

const NewsCard = ({ news }) => {
    const formattedDate = new Date(news.date).toLocaleTimeString('en-IN', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
    });

    // ‡§™‡•ç‡§∞‡§§‡•ç‡§Ø‡•á‡§ï ‡§ï‡§æ‡§∞‡•ç‡§° ‡§ï‡•á ‡§≤‡§ø‡§è ‡§è‡§ï ‡§Ö‡§¶‡•ç‡§µ‡§ø‡§§‡•Ä‡§Ø ‡§°‡§Æ‡•Ä ‡§∞‡§Ç‡§ó (Dummy Color)
    const categoryColorClass = news.category 
        ? `bg-${slugify(news.category).split('-')[0]}-600/10` 
        : 'bg-slate-200/50';

    return (
        <Link
            key={news.title}
            href={news.link}
            target="_blank" 
            rel="noopener noreferrer"
            // ‡§ï‡§æ‡§∞‡•ç‡§° ‡§ï‡§Ç‡§ü‡•á‡§®‡§∞: ‡§è‡§≤‡§ø‡§µ‡•á‡§ü‡•á‡§°, ‡§¨‡§æ‡§â‡§Ç‡§∏‡•Ä ‡§ü‡•ç‡§∞‡§æ‡§Ç‡§ú‡§ø‡§∂‡§®, ‡§è‡§Æ‡•ç‡§¨‡•â‡§∏ ‡§á‡§´‡§º‡•á‡§ï‡•ç‡§ü
            className="group flex flex-col rounded-2xl border border-slate-200 bg-white shadow-xl shadow-slate-200/50 transition-all duration-300 transform hover:scale-[1.03] hover:translate-y-[-2px] hover:border-emerald-500 hover:shadow-emerald-500/50 dark:border-slate-800 dark:bg-slate-900 dark:shadow-slate-950/60"
        >
            {/* ‡§ü‡•â‡§™ ‡§°‡§Æ‡•Ä ‡§á‡§Æ‡•á‡§ú/‡§µ‡§ø‡§ú‡§º‡•Å‡§Ö‡§≤ ‡§¨‡§æ‡§∞ (‡§Ü‡§á‡§ï‡§® ‡§ï‡•á ‡§∏‡§æ‡§•) */}
            <div className={`relative flex items-center justify-center h-20 ${categoryColorClass} rounded-t-2xl overflow-hidden`}>
                {/* ‡§µ‡§ø‡§ú‡§º‡•Å‡§Ö‡§≤ ‡§Ö‡§™‡•Ä‡§≤ ‡§ï‡•á ‡§≤‡§ø‡§è ‡§è‡§ï ‡§¨‡§°‡§º‡§æ ‡§Ü‡§á‡§ï‡§® */}
                 <TrendingUp className="w-10 h-10 text-emerald-600 opacity-60 dark:text-emerald-400" />
                 
                 {/* ‡§∏‡•ã‡§∞‡•ç‡§∏ ‡§î‡§∞ ‡§ï‡•à‡§ü‡•á‡§ó‡§∞‡•Ä ‡§ü‡•à‡§ó‡•ç‡§∏ ‡§ï‡•ã ‡§µ‡§ø‡§ú‡§º‡•Å‡§Ö‡§≤ ‡§¨‡§æ‡§∞ ‡§™‡§∞ ‡§ì‡§µ‡§∞‡§≤‡•á ‡§ï‡§∞‡•á‡§Ç */}
                 {news.news_source && (
                    <span className="absolute top-3 left-3 rounded bg-slate-900/80 px-2 py-1 text-[11px] font-semibold text-white shadow-md">
                        {news.news_source}
                    </span>
                 )}
            </div>

            {/* ‡§Æ‡•Å‡§ñ‡•ç‡§Ø ‡§∏‡§æ‡§Æ‡§ó‡•ç‡§∞‡•Ä ‡§ï‡•ç‡§∑‡•á‡§§‡•ç‡§∞ */}
            <div className="p-5 flex flex-col space-y-3 flex-grow">
                {/* ‡§Æ‡•á‡§ü‡§æ ‡§ü‡•à‡§ó‡•ç‡§∏: ‡§°‡•á‡§ü ‡§î‡§∞ ‡§ï‡•à‡§ü‡•á‡§ó‡§∞‡•Ä */}
                <div className="flex items-center gap-3 text-xs font-medium">
                    <span className="inline-flex items-center gap-1 text-slate-500 dark:text-slate-400">
                        <Clock className="w-3.5 h-3.5 text-emerald-500" />
                        {formattedDate}
                    </span>
                    <span className="inline-flex items-center rounded-full bg-emerald-100 px-2 py-0.5 text-[10px] font-bold uppercase tracking-wider text-emerald-800 dark:bg-emerald-900/50 dark:text-emerald-300">
                        {news.category || "General"} 
                    </span>
                </div>

                {/* ‡§∂‡•Ä‡§∞‡•ç‡§∑‡§ï (Title) - ‡§¨‡§°‡§º‡§æ ‡§î‡§∞ ‡§¨‡•ã‡§≤‡•ç‡§°, hover ‡§™‡§∞ ‡§∞‡§Ç‡§ó ‡§¨‡§¶‡§≤‡§§‡§æ ‡§π‡•à */}
                <h3 className="text-xl font-extrabold leading-snug text-slate-900 group-hover:text-emerald-700 transition dark:text-white dark:group-hover:text-emerald-400">
                    {news.title} 
                </h3>

                {/* ‡§∏‡§æ‡§∞‡§æ‡§Ç‡§∂ (Summary) - ‡§∏‡§æ‡§´‡§º ‡§î‡§∞ ‡§™‡§†‡§®‡•Ä‡§Ø */}
                <p className="text-sm leading-relaxed text-slate-600 dark:text-slate-400">
                    {news.summary || "No detailed summary available. Click to read the full article."} 
                </p>
            </div>

            {/* ‡§¨‡•â‡§ü‡§Æ ‡§è‡§ï‡•ç‡§∂‡§® (Source ‡§î‡§∞ Link) */}
            <div className="mt-auto p-5 pt-0">
                <div className="flex items-center justify-between text-sm font-semibold border-t border-slate-100 dark:border-slate-800 pt-4">
                    <span className="text-slate-500 dark:text-slate-400">
                        Read on <span className='font-bold text-slate-700 dark:text-slate-200'>{news.source}</span>
                    </span>
                    {/* ‡§Ü‡§ï‡§∞‡•ç‡§∑‡§ï ‡§µ‡§ø‡§ú‡§º‡•Å‡§Ö‡§≤ ‡§≤‡§ø‡§Ç‡§ï */}
                    <div className="inline-flex items-center gap-1.5 text-emerald-600 dark:text-emerald-400 group-hover:gap-2.5 transition-all duration-200">
                        View Article
                        <BookOpen className='w-4 h-4' />
                    </div>
                </div>
            </div>
        </Link>
    );
};


const MarketNewsSection = () => {
    const [allNews, setAllNews] = useState([]); 
    const [activeCategory, setActiveCategory] = useState("All");
    const [categories, setCategories] = useState([]);
    
    // Pagination State
    const [pagedNews, setPagedNews] = useState([]); 
    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);
    const [isLoading, setIsLoading] = useState(true);

    const [filteredNews, setFilteredNews] = useState([]); 

    // --- EFFECT 1: API ‡§∏‡•á ‡§°‡•á‡§ü‡§æ ‡§≤‡•ã‡§° ‡§ï‡§∞‡•á‡§Ç ---
    useEffect(() => {
        const loadNews = async () => {
            setIsLoading(true);
            const apiData = await fetchNews(1, 500); 

            if (apiData.news && apiData.news.length > 0) {
                setAllNews(apiData.news); 
                setCategories(extractCategories(apiData));
            } else {
                 setAllNews([]);
                 setCategories([]);
            }
            setIsLoading(false);
        };
        loadNews();
    }, []);
    
    // --- EFFECT 2: Category ‡§Ø‡§æ Page ‡§¨‡§¶‡§≤‡§®‡•á ‡§™‡§∞ News ‡§ï‡•ã ‡§´‡§º‡§ø‡§≤‡•ç‡§ü‡§∞ ‡§î‡§∞ ‡§™‡•á‡§ú ‡§ï‡§∞‡•á‡§Ç ---
    useEffect(() => {
        const filtered = allNews.filter((news) => {
            if (activeCategory === "All") return true;
            return news.category === activeCategory; 
        });
        
        const startIndex = (currentPage - 1) * DEFAULT_LIMIT;
        const endIndex = currentPage * DEFAULT_LIMIT;
        
        const paged = filtered.slice(startIndex, endIndex);
        
        setFilteredNews(filtered);
        setPagedNews(paged);
        setTotalPages(Math.ceil(filtered.length / DEFAULT_LIMIT));
        
    }, [activeCategory, allNews, currentPage]);


    const handlePageChange = (newPage) => {
        if (newPage >= 1 && newPage <= totalPages) {
            setCurrentPage(newPage);
            document.getElementById('market-news').scrollIntoView({ behavior: 'smooth' });
        }
    };

    return (
        <section
            id="market-news"
            // ‡§∏‡•á‡§ï‡•ç‡§∂‡§® ‡§¨‡•à‡§ï‡§ó‡•ç‡§∞‡§æ‡§â‡§Ç‡§°: ‡§π‡§≤‡•ç‡§ï‡§æ ‡§ó‡•ç‡§∞‡•á/‡§µ‡•ç‡§π‡§æ‡§á‡§ü, ‡§°‡§æ‡§∞‡•ç‡§ï ‡§Æ‡•ã‡§° ‡§∏‡§™‡•ã‡§∞‡•ç‡§ü
            className="border-b border-slate-200/70 bg-gray-50 py-16 sm:py-24 dark:border-slate-800/70 dark:bg-slate-950"
        >
            <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
                {/* Header and Filters Section */}
                <div className="flex flex-col gap-6 sm:flex-row sm:items-center sm:justify-between mb-12">
                    <div className='max-w-2xl'>
                        {/* ‡§∂‡•Ä‡§∞‡•ç‡§∑‡§ï (Main Title): ‡§¨‡§°‡§º‡§æ ‡§î‡§∞ ‡§Ü‡§ï‡§∞‡•ç‡§∑‡§ï ‡§Ü‡§á‡§ï‡§® ‡§ï‡•á ‡§∏‡§æ‡§• */}
                        <h2 className="text-3xl font-extrabold tracking-tight text-slate-900 dark:text-white">
                            <TrendingUp className="inline w-6 h-6 mr-2 text-emerald-600" />
                            Live Financial News Tracker
                        </h2>
                        <p className="mt-2 text-base text-slate-600 dark:text-slate-400">
                            Real-time market insights from authoritative sources, categorized for rapid analysis.
                        </p>
                    </div>
                    {!isLoading && allNews.length > 0 && (
                        <MarketNewsFilters
                            activeCategory={activeCategory}
                            setActiveCategory={(cat) => {
                                setActiveCategory(cat);
                                setCurrentPage(1); 
                            }}
                            categories={categories}
                        />
                    )}
                </div>

                {/* News Grid (Content Area) */}
                <div className="grid gap-8 grid-cols-1 sm:grid-cols-2 lg:grid-cols-3">
                    {isLoading ? (
                        <div className="col-span-full text-center py-20 text-emerald-600 font-semibold text-lg animate-pulse">
                            <Zap className="inline w-6 h-6 mr-2 animate-spin" />
                            Fetching latest market intelligence...
                        </div>
                    ) : pagedNews.length > 0 ? (
                        pagedNews.map((news, index) => (
                            <NewsCard key={index} news={news} /> 
                        ))
                    ) : (
                        <div className="col-span-full text-center py-20 text-slate-500 border rounded-xl p-4">
                            No news found in the selected category or on this page. Try selecting "All" or checking later.
                        </div>
                    )}
                </div>

                {/* Pagination Controls */}
                {pagedNews.length > 0 && totalPages > 1 && (
                    <div className="mt-12 flex justify-center items-center gap-4">
                        <button
                            onClick={() => handlePageChange(currentPage - 1)}
                            disabled={currentPage === 1}
                            className="flex items-center gap-1 px-5 py-2 text-sm font-semibold rounded-lg transition disabled:opacity-50 disabled:cursor-not-allowed bg-emerald-600 text-white hover:bg-emerald-700 shadow-md shadow-emerald-500/50"
                        >
                            <ChevronLeft className='w-4 h-4' /> Previous
                        </button>
                        
                        <span className="text-base font-medium text-slate-700 dark:text-slate-300">
                            Page <span className='font-bold text-emerald-600 dark:text-emerald-400'>{currentPage}</span> of {totalPages}
                        </span>

                        <button
                            onClick={() => handlePageChange(currentPage + 1)}
                            disabled={currentPage === totalPages}
                            className="flex items-center gap-1 px-5 py-2 text-sm font-semibold rounded-lg transition disabled:opacity-50 disabled:cursor-not-allowed bg-emerald-600 text-white hover:bg-emerald-700 shadow-md shadow-emerald-500/50"
                        >
                            Next <ChevronRight className='w-4 h-4' />
                        </button>
                    </div>
                )}
            </div>
        </section>
    );
};

export default MarketNewsSection;